<template>
  <v-card>
    <v-row align="center" justify="end" class="pr-4">
      <v-btn icon @click="closePdp">
        <v-icon dark>mdi-close-circle</v-icon>
      </v-btn>
    </v-row>
    <v-row class="ma-auto" style="width: 100%">
      <v-col class="ml-auto" cols="12" lg="6" md="6">
        <v-img
          height="400"
          contain
          :src="productsInfo.image"
          :lazy-src="productsInfo.image"
          :alt="productsInfo.description"
          class="px-3 mx-auto"
        >
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular indeterminate color="primary lighten-5"></v-progress-circular>
            </v-row>
          </template>
        </v-img>
      </v-col>
      <v-col class="mr-auto pa-3" cols="12" lg="6" md="6">
        <v-card-title class="title-pdp">{{productsInfo.name}}</v-card-title>
        <v-card-text class="mb-1 pdp-price">{{productsInfo.price | currency}}</v-card-text>
        <v-card-text class="pdp-description">{{productsInfo.description}}</v-card-text>
        <v-card-title class="pdp-title-secondary">Agregar instrucciones adicionales</v-card-title>
        <v-textarea v-model="comments" style="padding-top: 0px; padding-left: 16px; padding-right: 16px" placeholder="Instrucciones" auto-grow outlined rows="5" row-height="10"></v-textarea>
        <v-btn height="50" @click="addToCart" block color="primary">
        Agregar
        <v-icon right small>mdi-cart-outline</v-icon>
      </v-btn>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import { mapActions } from "vuex";
import { currencyFilter } from "@/filters";
export default {
  name: "Products",
  data() {
    return {
      comments: null
    };
  },
  methods: {
    ...mapActions("products/stores", ["addProductToCart"]),
    addToCart() {
      const data = {
        quantity: 1,
        comments: this.comments,
        id_product: this.productsInfo.id,
        id_business: this.productsInfo.stock.business_id
      };
      this.addProductToCart(data);
    },
    closePdp() {
      this.$emit("closePdp", false);
    }
  },
  props: {
    productsInfo: Object
  },
  filters: {
    currency: currencyFilter
  }
};
</script>
