
<template>
  <v-card hover :min-width="175" flat style="display:grid">
    <v-btn class="mt-6" @click="addToFavorites" color="primary" text small absolute top right fab>
      <v-icon v-if="productsInfo.favorites.length == 0">mdi-heart-outline</v-icon>
      <v-icon v-else>mdi-heart</v-icon>
    </v-btn>
    <v-img
    @click="dismissPdp(true)"
      height="150"
      width="150"
      contain
      :src="productsInfo.image"
      :lazy-src="productsInfo.image"
      :alt="productsInfo.description"
      class="px-3 mx-auto"
    >
      <template v-slot:placeholder>
        <v-row class="fill-height ma-0" align="center" justify="center">
          <v-progress-circular indeterminate color="primary lighten-5"></v-progress-circular>
        </v-row>
      </template>
    </v-img>
    <v-card-title @click="dismissPdp(true)" class="text-product">{{productsInfo.name}}</v-card-title>
    <v-card-text @click="dismissPdp(true)">
      <!-- <v-row
        align="center"
        class="mx-0"
      >
        <v-rating
          :value="4.5"
          color="amber"
          dense
          half-increments
          readonly
          size="14"
        ></v-rating>

        <div class="grey--text ml-4">4.5 (413)</div>
      </v-row>-->

      <div class="mb-1 text-price">{{productsInfo.price | currency}}</div>
      <!-- <div>{{productsInfo.description}}</div> -->
    </v-card-text>
    <!-- <v-divider class="mx-4"></v-divider> -->

    <v-card-actions>
      <v-btn @click="addToCart" width="100%" color="primary">
        Agregar
        <v-icon right small>mdi-cart-outline</v-icon>
      </v-btn>
    </v-card-actions>
    <v-dialog v-model="pdpDialog" max-width="700px">
      <pdp :productsInfo="productsInfo" @closePdp="dismissPdp"></pdp>
    </v-dialog>
  </v-card>
</template>

<script>
import { mapActions } from "vuex";
import { currencyFilter } from "@/filters";
import ProductDetailPage from "@/components/Products/ProductDetailPage";
export default {
  name: "Products",
  components: {
    Pdp: ProductDetailPage
  },
  data() {
    return {
      pdpDialog: null
    };
  },
  methods: {
    ...mapActions("products/stores", ["addProductToCart"]),
    ...mapActions("products/favorites", ["addProductToFavorites"]),
    addToCart() {
      const data = {
        quantity: 1,
        comments: "",
        id_product: this.productsInfo.id,
        id_business: this.productsInfo.stock.business_id
      };
      this.addProductToCart(data);
    },
    async addToFavorites() {
      const data = {
        id_product: this.productsInfo.id,
        id_business: this.productsInfo.stock.business_id
      };
      await this.addProductToFavorites(data);
      this.reloadData();
    },
    reloadData() {
      this.$emit("reloadData");
    },
    dismissPdp(value) {
      this.pdpDialog = value;
    }
  },
  props: {
    productsInfo: Object
  },
  filters: {
    currency: currencyFilter
  }
};
</script>
